import React, { Component } from "react";
import Panel from '../../public/AdminPanel';
import { Row, Col, Form, Input, Select, Button, message } from "antd";
import {
    AndroidOutlined, AppleOutlined, ChromeOutlined, CodeOutlined, BulbOutlined, FormatPainterOutlined
} from '@ant-design/icons';
const { Option } = Select;

export interface Props {

}

export interface State {
    valiId: "" | "success" | "error" | "warning" | "validating" | undefined,
    valiIdCard: "" | "success" | "error" | "warning" | "validating" | undefined,
    valiEmail: "" | "success" | "error" | "warning" | "validating" | undefined,
    valiPhone: "" | "success" | "error" | "warning" | "validating" | undefined,
}

class AddUser extends Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            valiId: undefined,
            valiIdCard: undefined,
            valiEmail: undefined,
            valiPhone: undefined
        };
    }

    onFinish = (values: any) => {
        console.log('Success:', values);
        message.error("")
    };

    handlePhoneChange = (e: any) => {
        const { value } = e.target;
        console.log(value);
        const reg = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/;
        if (reg.test(value)) {
            this.setState({
                valiPhone: "success"
            })
        } else {
            this.setState({
                valiPhone: "warning"
            })
        }
    }

    handleEmailChange = (e: any) => {
        const { value } = e.target;
        console.log(value);
        const reg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;
        if (reg.test(value)) {
            this.setState({
                valiEmail: "success"
            })
        } else {
            this.setState({
                valiEmail: "warning"
            })
        }
    }

    handleIdCardChange = (e: any) => {
        const { value } = e.target;
        console.log(value);
        const reg = /^(\d{6})(\d{4})(\d{2})(\d{2})(\d{3})([0-9]|X)$/;
        if ((!isNaN(value) && reg.test(value))) {
            this.setState({
                valiIdCard: "success"
            })
        } else {
            this.setState({
                valiIdCard: "warning"
            })
        }
    }

    handleIdChange = (e: any) => {
        const { value } = e.target;
        console.log(value);
        const reg = /[0-9]{10}/;
        if ((!isNaN(value) && reg.test(value))) {
            this.setState({
                valiId: "success"
            })
        } else {
            this.setState({
                valiId: "warning"
            })
        }
    }

    render() {
        return (
            <div>
                <Panel />
                <div className="admin-title"></div>
                <div className="usermanage-add-card card-shadow">
                    <p className="sub-title"></p>
                    <Form
                        onFinish={this.onFinish}
                    >
                        <Row className="usermanage-add-items">
                            <Col span={12} className="usermanage-add-item"></Col>
                            <Col span={12} className="usermanage-add-item"></Col>
                            <Col span={12} className="usermanage-add-item">
                                <Form.Item
                                    name="id"
                                    rules={[{ required: true, message: ' ' }]}
                                    hasFeedback
                                    validateStatus={this.state.valiId}
                                    help=" "
                                >
                                    <Input
                                        onChange={this.handleIdChange}
                                        placeholder=""
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={12} className="usermanage-add-item">
                                <Form.Item
                                    name="campus"
                                    rules={[{ required: true, message: ' ' }]}
                                >
                                    <Select
                                        placeholder=""
                                    >
                                        <Option value={0}></Option>
                                        <Option value={1}></Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12} className="usermanage-add-item"></Col>
                            <Col span={12} className="usermanage-add-item"></Col>
                            <Col span={12} className="usermanage-add-item">
                                <Form.Item
                                    name="name"
                                    rules={[{ required: true, message: ' ' }]}
                                >
                                    <Input
                                        placeholder=""
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={12} className="usermanage-add-item">
                                <Form.Item
                                    name="team"
                                    rules={[{ required: true, message: ' ' }]}
                                >
                                    <Select
                                        placeholder=""
                                    >
                                        <Option value={0}><AndroidOutlined /> Android</Option>
                                        <Option value={1}><AppleOutlined /> iOS</Option>
                                        <Option value={2}><BulbOutlined /> </Option>
                                        <Option value={3}><CodeOutlined /> </Option>
                                        <Option value={4}><ChromeOutlined /> </Option>
                                        <Option value={5}><FormatPainterOutlined /> </Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12} className="usermanage-add-item"></Col>
                            <Col span={12} className="usermanage-add-item"></Col>
                            <Col span={12} className="usermanage-add-item">
                                <Form.Item
                                    name="email"
                                    hasFeedback
                                    rules={[{ required: true, message: ' ' }]}
                                    validateStatus={this.state.valiEmail}
                                    help=" "
                                >
                                    <Input
                                        onChange={this.handleEmailChange}
                                        placeholder=""
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={12} className="usermanage-add-item">
                                <Form.Item
                                    name="auth"
                                    rules={[{ required: true, message: ' ' }]}
                                >
                                    <Select
                                        placeholder=""
                                    >
                                        <Option value={0}></Option>
                                        <Option value={1}></Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12} className="usermanage-add-item"></Col>
                            <Col span={12} className="usermanage-add-item"></Col>
                            <Col span={12} className="usermanage-add-item">
                                <Form.Item
                                    name="idcard"
                                    hasFeedback
                                    rules={[{ required: true, message: ' ' }]}
                                    validateStatus={this.state.valiIdCard}
                                    help=" "
                                >
                                    <Input
                                        onChange={this.handleIdCardChange}
                                        placeholder=""
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={12} className="usermanage-add-item">
                                <Form.Item
                                    name="phone"
                                    hasFeedback
                                    rules={[{ required: true, message: ' ' }]}
                                    validateStatus={this.state.valiPhone}
                                    help=" "
                                >
                                    <Input
                                        onChange={this.handlePhoneChange}
                                        placeholder=""
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={24} className="usermanage-add-item">
                                <Form.Item >
                                    <Button htmlType="submit"></Button>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </div>
            </div>
        );
    }
}

export default AddUser;